import { String } from "../../Model/String";
import { Injectable } from "@angular/core";
import { ServicesProvider } from "../services/services";
import { FilePdf2ImageListResponse } from "../../Model/Response/FilePdf2ImageListResponse";
import { FilePdf2ImageResponse } from "../../Model/Response/FilePdf2ImageResponse";
import { FilePdf2ImageViewModel } from '../../Model/ViewModel/FilePdf2ImageViewModel';
import { FileServicesProvider } from '../file-services/file-services';

/*
  Generated class for the FilePdf2ImageServiceProvider provider.

  See https://angular.io/guide/dependency-injection for more info on providers
  and Angular DI.
*/

@Injectable()
export class FilePdf2ImageServicesProvider extends FileServicesProvider {
  private FilePdf2Images: FilePdf2ImageListResponse;
  private FilePdf2Image: FilePdf2ImageResponse;
   ctl: string;

  constructor(
  public Services: ServicesProvider
  ) {
    super(Services);
    this.ctl = "FilePdf2Image";
    console.log("Hello FilePdf2ImageServiceProvider Provider");
  }

  public GetListsByAsync(PageSize: number, PageNumber: number, keyword: string,exec_group:string,exec_file_seq:number,order_type:boolean) {
    var sub_url: string;
    if(exec_file_seq===0)
    {
      sub_url = "FilePdf2Image?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&order_type={5}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,order_type);
    }
    else
    {
      sub_url = "FilePdf2Image/Select?PageSize={1}&PageNumber={2}&keyword={3}&exec_group={4}&exec_file_seq={5}&order_type={6}";
      sub_url = String.Format(sub_url, this.ctl, PageSize, PageNumber, keyword,exec_group,exec_file_seq,order_type);
    }
    return this.Services.GetAsync(sub_url,this.ctl,true).map(item => {
      this.FilePdf2Images = new FilePdf2ImageListResponse({
        Message: item.Message,
        DidError: item.DidError,
        ErrorMessage: item.ErrorMessage,
        PageSize: item.PageSize,
        PageNumber: item.PageNumber,
        TotalRows: item.TotalRows,
        PageRows: item.PageRows,
        TotalPages: item.TotalPages,
        Model: item.Model
      });
      return this.FilePdf2Images;
    });
  }



  public PostPutAsync(FilePdf2ImageViewModel:FilePdf2ImageViewModel,mode:string) {
    var sub_url: string;
    if(mode==="POST")
      sub_url = "FilePdf2Image";
    else
    sub_url = "FilePdf2Image/{1}";
    sub_url = String.Format(sub_url, this.ctl,FilePdf2ImageViewModel.exec_file_seq);
    return this.Services.PostPutAsync(sub_url, this.ctl, true,FilePdf2ImageViewModel,mode).map(
      item => {
        this.FilePdf2Image = new FilePdf2ImageResponse({
          Message: item.Message,
          DidError: item.DidError,
          ErrorMessage: item.ErrorMessage,
          Model: item.Model
        });
        return this.FilePdf2Image;
      }
    );
  }
}



