import { FileManagementViewModel } from "./FileManagementViewModel";

export class FilePdf2ImageViewModel extends FileManagementViewModel {
  /// <summary>
  ///  檔案功能處理主鍵
  /// <summary>
  public file_func_proc_key: string;
  /// <summary>
  ///  檔案功能處理
  /// <summary>
  public file_func_proc: string;

  /// <summary>
  ///  轉換檔案名稱
  /// </summary>
  public convert_to_filename: string;

  /// <summary>
  /// 轉換頁數（0:全部,-1:最後一頁,其他值:特定頁面）
  /// </summary>
  public page_index: number;

  /// <summar>
  /// 縮放比例
  /// </summary>
  public scale: number;

}



